import React, {useEffect, useState } from 'react'
import Spinner from './Spinner'
import NewsItem from './NewsItem'
import PropTypes from 'prop-types'
import InfiniteScroll from "react-infinite-scroll-component";



export default function News(props) {

    const [articles, setArticles] = useState([])
    const [loading, setLoading] = useState(false)
    const [page, setPage] = useState(1)
    const [totalResults, setTotalResults] = useState(0)
    

    const capitalizeFirstLetter = (string) => {
        return string.charAt(0).toUpperCase() + string.slice(1)
    }


    async function updateNews(){
        props.setProgress(10)
        const url = `https://newsapi.org/v2/top-headlines?country=${props.country}&category=${props.category}&apiKey=${props.apiKey}&page=${page}&pageSize=${props.pageSize}`
        setLoading(true)
        let data = await fetch(url)
        props.setProgress(30)
        let parsedData = await data.json()
        props.setProgress(80)
        setArticles(parsedData.articles)
        setTotalResults(parsedData.totalResults)
        setLoading(false)
        props.setProgress(100)
    }

    useEffect(() => {
        document.title = `${capitalizeFirstLetter(props.category)} - News`
        updateNews()
    }, [])

    // const handleNextPage = async () => {
    //     setPage(page+1)
    //     updateNews()
    // }

    // const handlePrevPage = async ()=> {
    //     setPage(page-1)
    //     updateNews()
    // }

    const fetchMoreData = async () => {
        
        const url = `https://newsapi.org/v2/top-headlines?country=${props.country}&category=${props.category}&apiKey=${props.apiKey}&page=${page+1}&pageSize=${props.pageSize}`
        setPage(page+1)
        let data = await fetch(url)
        let parsedData = await data.json()
        setArticles(articles.concat(parsedData.articles))
        setTotalResults(parsedData.totalResults)
        setLoading(false)

    }

    

        return (
            <div className="container my-3">
                <h1 className="text-center" style={{marginTop: '80px'}}>News App</h1>
                 {loading && < Spinner/>}
                 <InfiniteScroll
                    dataLength={articles.length}
                    next={fetchMoreData}
                    hasMore={articles.length !== totalResults}
                    loader={<Spinner/>}
                    >
                        <div className='container'>
                        <div className="row">
                            {articles.map((element) => {
                                return <div className="col-md-4" key={element.url}>
                                <NewsItem title={element.title? element.title.slice(0, 45): ''}    description={element.description? element.description.slice(0, 88) : ''} imageUrl={element.urlToImage? element.urlToImage: 'https://images.moneycontrol.com/static-mcnews/2021/09/Policy-diseases_pic.jpg'} newsUrl={element.url} author={element.author} date={element.publishedAt} source={element.source.name} />
                            </div>
                            
                            })}

                        </div>
                    </div>
                </InfiniteScroll>
                
            </div>
        )
    }



News.defaultProps = {
    country: 'in',
    pageSize: 10,
    category: 'general'
}

News.propTypes = {
    country: PropTypes.string,
    pageSize: PropTypes.number,
    category: PropTypes.string
} 